*, body{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}

.container{
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 10px;
}

.item-list{
  display: flex;
  gap: 30px;
  flex-wrap: wrap;
  justify-content: center;
}


.main{
  margin: 30px 0;
}

.row{
  display: flex;
  gap: 20px;
  margin: 20px 0 0 0;
  width: 100%;
}

.header-filters input{
  margin: 0 0 10px 0;
}

.color-filter{
  width: 150px;
  font-size: 20px;
}
.body-filters .title{
  font-weight: 600;
  font-size: 20px;
}

.range-filter{
  margin: 20px 0 0 0;
}
.range-filter-row{
  display: flex;
  margin: 10px 0 0 0;
  align-items: center;
}
.range-filter input{
  font-size: 16px;
  width: 60px;
  padding: 5px 10px;
  margin: 0 20px 0 0;
}
.range-filter input:last-child{
  margin: 0;
}
.range-filter-row{
  position: relative;
}
.range-filter span{
  position: absolute;
  width: 10px;
  height: 2px;
  background-color: #000;
  display: block;
  left: 50%;
  transform: translateX(-100%);

}

@media(max-width:675px){
  .range-filter span{
    transform: translateX(-50%);
  }
}

.items{
  flex: 1 1 auto;
}