.filter{
    margin-bottom: 10px;
}



label input{
    align-self: flex-start;
    margin: 3px 10px 0 0;
}

label span{
    user-select: none;
}

@media(max-width:800px){
    label input{
        margin: 5px 10px 0 0;
    }
}

.radio label{
    position: relative;
    padding: 10px 20px;
    background-color: #f5f5f5;
    transition: background-color 0.3s, color 0.3s;
    user-select: none;
    cursor: pointer;
  }
  .radio label._checked{
    background-color: #0e0e0e;
    color: #fff;
  }
  
  .radio label input[type="radio"]{
    display: none;
  }
  .notFoundImage {
    text-align: center;
    margin: 60px 0 0 0;
  }
  .addTask{
    margin: 50px 0 0 0;
  }
  .notFoundImage-text{
    font-size: 25px;
    position: relative;
    z-index: 2;
    font-weight: 500;
  }
  .notFoundImage img{
    width: 350px;
    position: relative;
    top: -5px;

  }

  .checkbox-filter{
    display: flex;
  }
  .radio label{
    display: flex;
    font-size: 18px;
    margin-left: 5px;
  }
  .radio label:first-child{
    margin: 0;
  }
  .checkbox-filter label{
    display: flex;
    font-size: 18px;
    margin-left: 5px;
}

.header-filters input{
    width: 100%;
    padding: 10px 20px;
    font-size: 16px;
}



.radiosort{
    display: flex;
    gap: 20px;
}

@media(max-width:675px){
    .radiosort{
        flex-wrap: wrap;
        justify-content: center;
    }
    .row{
        flex-direction: column;
    }
    .color-filter{
        width: 100%;
        display: flex;
        gap: 10px;
        flex-wrap: wrap;
    }
    .range-filter{
        display: flex;
        align-items: center;
        gap: 10px;
    }
    .range-filter-row{
        margin: 0;
    }
}