.item {
    border: 1px solid #ccc;
    border-radius: 8px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1);
    overflow: hidden;
    margin: 16px;
    width: 300px;
    transition: transform 0.2s;
  }
  
  .item:hover {
    transform: scale(1.02);
  }
  
  .item-header img {
    width: 100%;
    height: 320px;
    object-fit: cover;
    border-bottom: 1px solid #ccc;
  }
  
  .item-body {
    padding: 16px;
    background-color: #fff;
  }
  
  .item-title {
    font-size: 1.5em;
    font-weight: bold;
    margin: 0;
    color: #333;
  }
  
  .item-description {
    font-size: 1em;
    color: #666;
    margin: 8px 0;
  }
  
  .item-props {
    list-style-type: none;
    padding: 0;
    margin: 8px 0 0;
  }
  
  .item-props li {
    margin: 4px 0;
    font-weight: bold;
  }
  
  .item-props span {
    font-weight: normal;
  }